* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.Header {
  display: flex;
  justify-content: space-between;
  background-color: black;
  color: white;
  height: 70px;
  padding-top: 10px;
}
.Header .input1 {
  width: 800px;
  height: 50px;
  font-size: 30px;
  color: black;
  padding-left: 20px;
}
.Header .span2 {
  width: 60px;
  height: 50px;
  margin-right: 10px;
}
.Header .button1 {
  width: 50px;
  height: 50px;
  margin-left: -19px;
  padding: 0 0px 0 -20px;
}
.div {
  background-color: black;
  color: white;
  height: 50px;
  padding-top: 10px;
}
.spanMenu {
  margin-left: 20px ;
}
.SiderBar {
  margin: 20px 0 0 50px;
  width: 200px;
}
.Product {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(100, 1fr);
  margin-left: 100px ;
  row-gap: 40px;
}
.Product .ProductList {
  padding-left: 40px;
  display: grid;
  row-gap: 20px;
}
.Product .ProductList .img {
  width: 300px;
  height: 300px;
}
.Product .ProductList .pList1 {
  width: 300px;
}
.VIP {
  display: flex;
}
